.App {
  text-align: center;
  background-color: #478d6b;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.App-header {
  font-size: 2rem;
  font-weight: bold;
}

.profile-picture {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin-top: 20px;
  object-fit: cover;
}

h1, h2 {
  margin-bottom: 20px;
}

ul {
  list-style-type: none;
  padding: 0;
}

a {
  color: #00ccff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

ul li {
  margin: 10px 0;
}

ul li a {
  font-size: 1.2rem;
}

/* Shelf Styles */
.shelf {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 40px;
}

.shelf-item {
  background-color: #ffffff22; /* semi-transparent white background */
  padding: 10px;
  border-radius: 8px;
  width: 150px;
  text-align: center;
}

.shelf-item img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 4px;
}

.shelf-item p {
  margin-top: 10px;
  font-size: 1.1rem;
  color: #fff;
}
